<template>
  <div>
    <van-nav-bar title="找回密码" @click-left="Return" class="bgc">
      <van-icon name="arrow-left" slot="left" />
    </van-nav-bar>

    <div class="content">
      <van-form @submit="onSubmit">
        <van-field
          required
          colon
          v-model="userInf.phone"
          type="tel"
          :name="userInf.phone"
          label="手机号"
          maxlength="11"
          placeholder="注册时的手机号"
          :rules="[{ required: true, message: '请输入注册时的手机号' }]"
        />
        <van-field
          required
          colon
          v-model="userInf.password"
          type="password"
          :name="userInf.password"
          label="新密码"
          placeholder="新密码"
          :rules="[{ required: true, message: '请输入新密码' }]"
        />

        <van-field
          label="邮箱"
          required
          v-model="userInf.email"
          :name="userInf.email"
          placeholder="正则校验"
          :rules="[{ pattern, message: '请输入正确内容' }]"
        />
        <!-- 
        <van-field
          required
          v-model="userInf.validCode"
          center
          clearable
          label="验证码"
          placeholder="请输入验证码"
        >
          <template #button>
            <van-button size="small" type="primary" :disabled="isCheck" @click="checkValid">发送验证码</van-button>
          </template>
        </van-field>-->
        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit">提交</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import "../../assets/css/commaint.less";
export default {
  data() {
    return {
      userInf: {
        phone: "",
        password: "",
        validCode: "",
        email: ""
      },

      // 邮箱验证
      pattern: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,

      // 验证按钮
      isCheck: true
    };
  },

  created() {},
  methods: {
    // 返回
    Return() {
      this.$router.go(-1);
    },

    onSubmit() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/retrievePassword",
        data: {
          appkey: this.appkey,
          phone: this.userInf.phone,
          password: this.userInf.password
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == "L001") {
            this.$toast(res.data.msg);
            this.$router.push({ name: "Login" });
            
          } else {
            this.$toast(res.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
      
    },

    checkValid() {
      
    }
  }
};
</script>

<style lang="less" scoped>
</style>